.header{
  border-bottom:1px solid #302F2F;
  &-wrapper{
    &__content{
      display: flex;
      justify-content: space-between;
    }
    &__block{
      &-settings{
        margin-left: 120px;

        width: 100%;
      }
    }
  }
  &-settings{
    &__wrapper{
      display: flex;
      align-items: center;
    }
    &__block{
      & + &{
        margin-left: 62px;
      }
    }
  }
  &-nav{
    &__list{
      display: flex;
      align-items: center;
    }
  }
  &__wrapper{
    background-color: $blackDarkGrey;
  }
  &__logo{
    display: flex;
    align-items: center;
    justify-content: center;

    height: 100%;
  }
  &__row{
    &-settings{
      display: flex;
      align-items: center;
      justify-content: flex-end;

      height: 76px;
    }
    &-nav{
      height: 76px;

      display: flex;
      align-items: center;
      justify-content: center;
    }
  }
}

.logo{
  &__wrapper{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
  &__header{
    width: 145px;
    height: 80px;

    flex-shrink: 0;
  }
  &__img{
    width: 100%;
    height: 100%;
    object-fit: contain;
  }
  &__text{
    font-family: 'Exo', sans-serif;
    font-weight: 900;
    font-size: 10px;

    letter-spacing: 0.05em;
    text-transform: uppercase;

    color: #FFFFFF;
  }
}

.profile{
  &-link{
    &__wrapper{
      display: flex;
      align-items: center;
    }
    &__icon{
      width: 22px;
      height: 22px;

      flex-shrink: 0;
    }
    &__content{
      margin-left: 10px;
    }
    &__text{
      font-family: 'PF BeauSans Pro', sans-serif;
      font-size: 14px;
      font-weight: 400;

      color:#fff;
    }
  }
}

.nav{
  &-list{
    &__item{
      & + &{
        margin-left: 92px;
      }
    }
  }
  &-item{
    &__link{
      font-family: 'PF BeauSans Pro', sans-serif;
      font-weight: 300;

      font-size: 18px;
      color:#e5e5e5;

      &-bold{
        font-weight: 700;
      }
    }
  }
}