}

.partners-list__wrapper {
  display: flex;
}

.partners-list__item {
  counter-increment: partners-counter;
}

.partners-list__item-icon {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 50px;
  height: 50px;
  flex-shrink: 0;
}

.partners-list__item-content {
  margin: 20px 0 0 0;
  max-width: 130px;
}

.partners-list__item-content::before {
  content: counter(partners-counter) ". ";
