}

.faq-form__button {
  margin-left: 11px;
}

.faq-form__button-item {
  width: 240px;
}

.faq-tags__wrapper {
  display: flex;
  flex-wrap: wrap;
  margin: -8px;
}

.faq-tags__item {
  margin: 4px;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 1px solid #A7A7A7;
  border-radius: 500px;
  height: 40px;
  padding: 0 20px;
  color: #A7A7A7;
}

.faq-tags__item.active {
