  border-color: #E63739;
}

.faq-tags__text {
  font-family: 'PF BeauSans Pro', sans-serif;
  font-weight: 700;
  font-size: 16px;
  line-height: 40px;
  color: inherit;
}

.faq-wrapper__item + .faq-wrapper__item {
  border-top: 1px solid #444;
}

.faq-item__wrapper {
  padding: 75px 0;
