}

.section-hero__wrapper {
  position: relative;
}

.section-hero__wrapper::after {
  content: '';
  position: absolute;
  left: 0;
  bottom: 0;
  z-index: 4;
  width: 100%;
  height: 137px;
  background: linear-gradient(180deg, rgba(0, 0, 0, 0) 3.65%, rgba(0, 0, 0, 0.3) 100%);
}

.section-hero__footer {
  position: absolute;
  z-index: 5;
  left: 0;
  bottom: 40px;
  width: 100%;
}

.hero-slider__item {
  position: relative;
}

.hero-slider__item-wrapper {
  position: relative;
  height: calc(100vh - 152px);
  min-height: 610px;
}

.hero-slider__item-img {
  position: absolute;
  left: 0;
  top: 0;
  z-index: 2;
  width: 100%;
  height: 100%;
}

.hero-slider__item-content {
  position: relative;
  z-index: 3;
  display: flex;
  align-items: center;
