.footer{
  &__row{
    &-black{
      background: #0B0B0B;
    }
    &-bordered{
      border-top: 1px solid #302F2F;
    }
    &-first{
      padding: 66px 0;
    }
    &-second{
      height: 71px;
      display: flex;
      align-items: center;
      background: #302F2F;
    }
    &-wrapper{
      display: flex;
      justify-content: space-between;
      &-center{
        align-items: center;
      }
    }
  }
  &__logo{
    display: flex;
    align-items: flex-start;
  }
  &__contacts{
    margin-top: 18px;
  }
  &__list{
    &-link{
      font-family: 'PF BeauSans Pro', sans-serif;
      font-size: 16px;
      line-height: 32px;
      color: #E5E5E5;
    }
  }
  &__rights{
    display: flex;
    align-items: center;
    &-block{
      & + &{
        margin-left: 30px;
      }
    }
  }
  &__text{
    font-family: 'PF BeauSans Pro', sans-serif;
    font-size: 14px;
    line-height: 14px;
    color: #E5E5E5;
  }
  &-form{
    &__wrapper{
      display: flex;
      flex-direction: column;

      width: 269px;
    }
    &__title{
      font-family: 'PF BeauSans Pro', sans-serif;
      font-weight: 700;
      font-size: 18px;
      color: #FAFAFA;

      text-transform: capitalize;
    }
    &__content{
      margin:14px 0 0 0;

      display: flex;
      flex-direction: column;
    }
    &__block{
      width: 100%;
      & + &{
        margin: 8px 0 0 0;
      }
    }
    &__footer{
      margin: 16px 0 0 0;
    }
  }
}

.contacts{
  &-list{
    &__item{
       & + &{
         border-top: 1px solid #302F2F;
       }
    }
  }
  &-item{
    &__wrapper{
      display: flex;
      align-items: center;

      height: 50px;
      width: 212px;
    }
    &__icon{
      width: 20px;
      height: 20px;

      flex-shrink: 0;
    }
    &__link{
      font-family: 'PF BeauSans Pro', sans-serif;
      font-weight: 400;
      font-size: 14px;

      color: #E5E5E5;
    }
    &__content{
      margin-left: 19px;
      width: 100%;
    }
  }
  &__select{
    .custom-select{
      &-opener{
        padding-right: 0;
        padding-left: 0;

        border: none;
      }
    }
  }
}

.form{
  &__input{
    -webkit-appearance: none;

    width: 100%;
    height: 40px;

    border: 1px solid #A7A7A7;
    border-radius: 5px;

    padding: 0 13px;

    font-family: 'PF BeauSans Pro', sans-serif;
    font-weight: 400;
    font-size: 14px;
    color: #E5E5E5;

    background: none;
    outline: none;
    &::placeholder{
      opacity: 1;
    }
  }
}

.social{
  &-block{
    &__list{
      display: flex;
      align-items: center;
      justify-content: center;
    }
    &__wrapper{
      display: flex;
      align-items: center;
    }
    &__content{
      margin-left: 37px;
    }
    &__title{
      &-label{
        font-family: 'PF BeauSans Pro', sans-serif;
        font-size: 14px;
        line-height: 1;
        color: #E5E5E5;
      }
    }
  }
  &-list{
    &__item{
      & + &{
        margin-left: 22px;
      }
    }
    &__link{
      display: flex;
      align-items: center;
      justify-content: center;

      width: 19px;
      height: 19px;
    }
  }
}