.container{
  max-width: 1258px;
  width: 100%;

  margin: 0 auto;
}

.icon{
  width: 100%;
  height: 100%;
}

.img{
  &-full{
    width: 100%;
    height: 100%;
  }
}

.button{
  -webkit-appearance: none;

  outline: none;
  border: none;

  width: 100%;
  height: 46px;
  line-height: 46px;


  display: flex;
  align-items: center;
  justify-content: center;
  &:hover{
    cursor: pointer;
  }
  &-accent{
    font-family: 'PF BeauSans Pro', sans-serif;
    font-weight: 700;
    font-size: 16px;

    color:#fff;
    background: #E63739;
    border-radius: 5px;

    box-shadow: 0px 4px 10px rgba(49, 15, 15, 0.25);
  }
  &-grey{
    font-family: 'PF BeauSans Pro', sans-serif;
    font-weight: 700;
    font-size: 16px;

    color:#fff;
    background: #302F2F;
    box-shadow: 0px 4px 10px rgba(49, 15, 15, 0.25);
    border-radius: 5px;
  }
  &-white{
    font-family: 'PF BeauSans Pro',sans-serif;
    font-weight: 700;
    font-size: 16px;
    line-height: 19px;
    text-align: center;

    color: #FAFAFA;

    border: 1px solid #FAFAFA;
    border-radius: 5px;
  }
  &-medium{
    max-width: 300px;
  }
  &-small{
    max-width: 275px;
  }
  &-big{
    max-width: 400px;
  }
}

.list{
  &-accent{
    font-family: 'PF BeauSans Pro', sans-serif;
    font-size: 16px;
    line-height: 24px;

    color:#E5E5E5;
    li{
      &::before{
        content: '\2022';
        color: #E63739;

        display: inline-block;

        margin: 0 10px 0 0 ;
      }
    }
  }
}

.tabs{
  &-content{
    &__item{
      display: none;
      &.active{
        display: block;
      }
    }
  }
}